import { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import ProductCard from '../components/product/ProductCard';
import HeroSection from '../components/home/HeroSection';
import { Product } from '../types';
import { fetchPrintifyProducts } from '../services/printify';
import { fetchProducts as fetchFallbackProducts } from '../data/products';

export default function HomePage() {
  const [products, setProducts] = useState<Product[]>([]);
  const [heroProduct, setHeroProduct] = useState<Product | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    async function loadProducts() {
      setLoading(true);
      try {
        // First try to fetch from Printify API via Netlify function
        let data = await fetchPrintifyProducts();
        
        // If no products returned, use fallback mock data
        if (!data || data.length === 0) {
          console.log('[HomePage] No products from API, using fallback data');
          data = await fetchFallbackProducts();
        }
        
        // Set the first product (most recent) as hero
        if (data.length > 0) {
          setHeroProduct(data[0]);
          // Show next 4 products in featured section
          setProducts(data.slice(1, 5));
        }
      } catch (error) {
        console.error('[HomePage] Error loading products:', error);
        // On error, use fallback data
        const fallbackData = await fetchFallbackProducts();
        if (fallbackData.length > 0) {
          setHeroProduct(fallbackData[0]);
          setProducts(fallbackData.slice(1, 5));
        }
      } finally {
        setLoading(false);
      }
    }
    loadProducts();
  }, []);

  return (
    <div className="min-h-screen bg-gradient-bollywood">
      {/* Hero Section */}
      <HeroSection heroProduct={heroProduct} loading={loading} />

      {/* Add a smooth transition section */}
      <div className="relative -mt-20 z-10">
        <div className="absolute inset-0 bg-gradient-to-b from-transparent via-orange-50 to-[#ffe4cc]"></div>
        <div className="relative h-32"></div>
      </div>

      {/* Featured Products section with updated background */}
      <section className="relative bg-gradient-to-b from-[#ffe4cc] to-[#fff0e5] pb-20">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.5 }}
            className="text-center mb-12"
          >
            <h2 className="text-4xl font-bold text-gray-900 mb-4">
              FEATURED CULT CLASSICS
            </h2>
            <p className="text-lg text-gray-600">
              Iconic designs from cinema's most memorable moments
            </p>
          </motion.div>

          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8">
            {products.slice(0, 4).map((product, index) => (
              <motion.div
                key={product.id}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: 0.6 + index * 0.1 }}
              >
                <ProductCard product={product} />
              </motion.div>
            ))}
          </div>

          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ delay: 1 }}
            className="text-center mt-12"
          >
            <Link
              to="/shop"
              className="inline-flex items-center gap-2 bg-gray-900 text-white px-8 py-3 rounded-full hover:bg-gray-800 transition-colors"
            >
              View All Products
              <FiArrowRight />
            </Link>
          </motion.div>
        </div>
      </section>

      {/* Add a subtle divider before the next section */}
      <div className="h-px bg-gradient-to-r from-transparent via-orange-200 to-transparent"></div>
    </div>
  );
}